.bayer-services-container {
  display: flex;
  justify-content: center;
  align-items: center;

}

.bayer-services-content {
  width: 330px;
  max-height: 850px;
  padding: 10px 5px;
  box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
  display: flex;
  flex-direction: column;
}

.bayer-services-content h3 {
  text-align: center;
  /* margin-bottom: 10px; */
}

.btns-bayer-services{
display: flex;
justify-content: space-between;
margin-top: 10px;

}
.btn-bayer-services-send-form{
  margin-top: 25px;
}

.btn-bayer-services-back{
  width: 100%;
}

.btn-bayer-services-shopping{
  padding: 0 50px;
}

.ant-form-item {
  margin-bottom: 0;
}

.ant-upload-picture-card-container {
  display: flex;
  flex-direction: row;
}

.ant-upload-wrapper.ant-upload-picture-card-wrapper
  .ant-upload.ant-upload-select {
  width: 70px;
  height: 70px;
}
